import React from 'react';
import { Card, CardContent, Typography, Grid} from '@material-ui/core';
import Countup from 'react-countup';
import cx from 'classnames';

import styles from './Cards.module.css';

const Cards =  ({ data : {confirmed, recovered, deaths, lastUpdate} } ) => {
    if(!confirmed){
        return "Loading...";
    }
    return(
        <div className = {styles.container}>
            <Grid container spacing={3} justify="center">
                <Grid item component={Card} className={cx (styles.card,styles.infected)} xs={12} md={3}>
                    <CardContent>
                        <Typography color="textSecondary" gutterBottom>Infected</Typography>
                        <Typography variant="h5" >
                            <Countup start={0} end={confirmed.value} duration={5.0} separator="," delay={0.2}/>
                        </Typography>
                        <Typography variant="body2">Active cases of Covid 19</Typography>
                        <Typography color="textSecondary">{new Date(lastUpdate).toDateString()}</Typography>
                    </CardContent>
                </Grid>

                <Grid item component={Card} className={cx (styles.card,styles.recovered)} xs={12} md={3}>
                    <CardContent>
                        <Typography color="textSecondary" gutterBottom>Recovered</Typography>
                        <Typography variant="h5" >
                            <Countup start={0} end={recovered.value} duration={5.0} separator="," delay={0.2}/>
                        </Typography>
                        <Typography variant="body2">Recovered cases from Covid 19</Typography>
                        <Typography color="textSecondary">{new Date(lastUpdate).toDateString()}</Typography>
                    </CardContent>
                </Grid>

                <Grid item component={Card} className={cx (styles.card,styles.deaths)} xs={12} md={3}>
                    <CardContent>
                        <Typography color="textSecondary" gutterBottom>Deaths</Typography>
                        <Typography variant="h5" >
                            <Countup start={0} end={deaths.value} duration={5.0} separator="," delay={0.2}/>
                        </Typography>
                        <Typography variant="body2">Number of deaths of Covid 19</Typography>
                        <Typography color="textSecondary">{new Date(lastUpdate).toDateString()}</Typography>
                    </CardContent>
                </Grid>

            </Grid>
        </div>
    );
}
export default Cards;